@keyframes GradientBG{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}#navbar{position:absolute;left:0;right:0;box-shadow:0px 14px 80px rgba(34,35,58,0.2)}.inner{width:450px;margin:auto;background:#ffffff;box-shadow:0px 14px 80px rgba(34,35,58,0.4);padding:40px 55px 45px 55px;border-radius:15px;transition:all .3s}.inner .form-control:focus{border-color:#167bff;box-shadow:none}.inner h3{text-align:center;margin:0;line-height:1;padding-bottom:20px}.custom-control-label{font-weight:400}.forgot-password,.forgot-password a{text-align:right;font-size:13px;padding-top:10px;color:#7f7d7d;margin:0}.forgot-password a{color:#167bff}#main{margin:0 auto;margin-top:30px;padding:20px 25px;border-radius:5px;max-width:850px;background-color:rgba(255,255,255,0.95)}#main .title{margin-top:15px}#main .title h1{width:100%;text-align:center;font-size:2rem;font-weight:500}#main .addForm{margin:20px 0}#main .table-container{margin-bottom:15px;background-color:white;border-radius:5px;overflow-x:hidden}#main .total h3 span{padding:10px 15px;font-weight:300;font-size:1.5rem}#table{margin:0}#table thead tr th{font-size:1.2rem;font-weight:500;font-family:"Open Sans", sans-serif}#table thead tr th:first-child{text-align:center}#table thead tr th:last-child{text-align:center}#table tbody .table-row td .delete-icon{transition:color 0.2s ease}#table tbody .table-row td .delete-icon:hover{cursor:pointer;color:red}#table tbody .table-row td:first-child{text-align:center}#table tbody .table-row td:last-child{text-align:center}#table tbody .table-row .expense{background-color:#F44336}#table tbody .table-row .income{background-color:#B2FF59}*{font-family:"Lato", sans-serif}html,body,#root{height:100%}body{background-color:#8EC5FC;background:linear-gradient(239deg, #2193b0, #6dd5ed);background-size:400% 400%;animation:GradientBG 25s ease infinite}.flex-container{height:100%;display:flex;justify-content:center;align-items:center}

